.user-details-page {
    width: rem(400px);
    display: flex;
    flex-direction: column;
    justify-self: center;
    background-color: $clr2;
    padding: rem(10px);
    border-radius: rem(10px);
    gap: rem(20px);
    img {
        width: rem(200px);
        align-self: center
    }

    @include for-mobile-layout {
        width: 100%;
        gap: rem(10px);
        img {
            width: rem(150px);
            align-self: center;
        }
    }
}
