.contact-preview {
    // width: 100%;
    display: flex;
    justify-content: space-around;

    flex-direction: column;
    border: 1px solid $clr5;
    background-color: $clr2;
    padding: rem(10px);
    border-radius: rem(10px);
    align-items: center;
    width: 150px;
    height: 150px;

    img {
        width: rem(80px);
        height: rem(80px);
        object-fit: cover;
        border-radius: 50%;
    }
    p {
        font-weight: 600;
        margin: 0 auto;
    }
    @include for-mobile-layout {
        width: 80px;
        height: 80px;
        padding: rem(4px);
        img {
            width: rem(40px);
            height: rem(40px);
            object-fit: cover;
            border-radius: 50%;
        }
        p{
            font-size: rem(10px);
        }
    }
}
