.rates-list {
    text-align: center;
    padding: rem(10px);
    background-color: $clr2;
    border-radius: rem(10px);
    border: 1px solid black;
    width: fit-content;
    h2 {
        margin-bottom: rem(20px);
        // font-size: 3.75rem;
        font-weight: 300;
    }
    h3 {
        color: #258734;
    }

    .currency-list {
        display: flex;
        justify-content: space-evenly;
        gap: rem(100px);
        .usd-rate,
        .eur-rate,
        .gbp-rate,
        .cny-rate {
            display: grid;
            grid-template-rows: 0.5fr repeat(2, 0.25fr);
            // height: rem(60px);
            // width: rem(80px);
            align-items: center;
            img {
                width: rem(60px);
                justify-self: center;
            }
        }
    }
    @include for-mobile-layout {
        padding: rem(5px);
        width: 100%;

        h2 {
            font-size: smaller;
            font-weight: 600;
        }

        .currency-list {
            gap: rem(10px);
            .usd-rate,
            .eur-rate,
            .gbp-rate,
            .cny-rate {
                img {
                    width: 30px;
                }
                
                align-items: center;
                height: rem(80px);
                width: rem(80px);
            }
        }
    }
}
