.contact-details-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: rem(20px);
    padding: 0;

    .contact-details {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: rem(10px);
        border-radius: rem(10px);
        background-color: $clr2;
        gap: rem(10px);
        // .contact-info {
        //     grid-column: 2/-1;
        //     grid-row: 1/-1;
        // }

        .contact-img-details {
            display: flex;
            img {
                width:  300px;
                object-fit: cover;
            }
        }

        .edit-logo {
            background-color: $clr2;
            position: absolute;
            right: 5px;
            top: 5px;
            display: flex;
            justify-content: space-between;
            border-radius: rem(10px);
            padding: rem(5px);
            img {
                width: rem(20px);
            }
        }
    }

    @include for-mobile-layout {
        align-items: unset;
        gap: rem(10px) 0;
        .contact-details {
            flex-direction: row;
            align-items: center;
            gap: rem(40px);
            .contact-img-details {
                // align-self: center;
                .contact-img {
                    width: 100px;
                }
            }
            .edit-logo {
                img {
                    width: rem(20px);
                }
            }
        }

    }
}
